*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;

}
body{
    font-size: 20px;
    overflow-x: hidden;
    overflow-y: hidden;

    color: #fff;
}

header{
    background-image:linear-gradient(rgba(0,0,0,0.6),rgba(0,0,0,0.6)) , url("Cartoon.png");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    height: 100vh;
    width: 100%;
}



.row{
    max-width: 1180px;
    margin: 0 auto;
    
}
.logo{
    /* margin-left: 20px; */
}
.clearfix::after{
    content: ".";
    visibility: hidden;
    display: block;
    height: 0px;
    clear: both;
    }
.logo{
    height: 120px;
    width: auto;
    float: left;
    margin-top: 20px;

}

.main_nav{
    float:right;
    margin-top: 60px;

}
.main_nav li{
    display: inline-block;
    list-style: none;
    cursor: pointer;
    margin-left: 40px;
    
    
}
.main_nav li a{
    color: #fff;
    font-weight: 100;
    padding: 5px 0;
    text-transform: capitalize;
    font-size: 90%;
    text-decoration: none;
    transition: hover 0.5s linear;

}
.main_nav li :hover{
    color:  #bf55ec;


}

/* //body */
.main_content_header{
    width: 1180px;
    position: absolute;
    top: 57%;
    left: 50%;
    /* text-align: center; */
    transform: translate(-50%,-50%);

}
h1{
    color: #fff;
    font-size: 240%;
    word-spacing: 5px;
    letter-spacing: 3px;
    margin-bottom: 20px;
    text-transform: capitalize;
}

.colorChange{
    animation: colorchange 2s infinite linear;
}
@keyframes colorchange {
    0%{
        color: red;
    }

    25%{
        color: #bf55ec;

    }
    50%{color: #fff;}
  
    100%{
    color: #bf55ec;
    }
}



.btns{
    display: inline-block;
    padding: 10px 30px;
    cursor: pointer;
    font-weight: lighter;
    text-decoration: none;
    text-transform: capitalize;
    text-decoration: none;
    border-radius: 200px;
    transition: background-color 0.2s,border 0.2s,color 0.2s;
}
.btn_full{
    background-color: transparent;
    color: #bf55ec;
    margin-right: 15px;
    border: 1px solid #bf55ec;
}
.btn_full:hover{
    color:#bf55ec;
    background-color: #fff;
    
}
.btn_full_2{

    background-color: transparent;
    color: #fff;
    margin-left: 15px;
    border: 1px solid #fff;

}
.btn_full_2:hover{
    background-color: #bf55ec;
    border: #bf55ec;
}

.hamburger{
    display: none;
}

.categ{
    display: flex;
    max-width: 100%;
    height: 70px;
    position: relative;
    z-index: 2;
    top: 65%;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    height: 100px;
    /* border: 2px solid black; */
}

.photo{
    /* border: 2px solid black; */
    cursor: pointer;
    height: 50px;
    width: 50px;
    margin: 5px;
    box-shadow: 2px 3px 5px gray;
    
}

.photo:hover{
    border: 2px solid #bf55ec;
}
/* Responsive */

@media only screen and (max-width:1180px) {

    .main_content_header{
        width: 100%;
        padding:0 3%;
    }
}


@media only screen and (max-width:998px) {
    h1{
        font-size: 200%;
    }

   
}
@media only screen and (max-width:768px) {
    h1{
        font-size: 180%;
    }
    .main_nav{
        display:none;
    }
    .main_nav{
        float: left;
    }
    .hamburger{
        display: inline-block;
        float: right;
        color: #fff;
        text-decoration: none;
        margin-top: 30px;
        margin-right: 20px;
    }
    .main_nav li{
        margin-top:10px ;
        display: block;
    }

   
}

@media only screen and (max-width:480px) {
    .btn_full{
        margin-bottom: 20px;
    }
    .btns{
        margin-left: 10px;
    }
    h1{
        margin-left: 20px;
        font-size: 150%;
    }
}